import React from 'react';
import { keyframes } from '@mui/material/styles';
import { Box } from '@mui/material';

const float = keyframes`
  0% {
    transform: translateY(0px) rotate(0deg);
    opacity: 0.7;
  }
  50% {
    transform: translateY(-20px) rotate(180deg);
    opacity: 1;
  }
  100% {
    transform: translateY(0px) rotate(360deg);
    opacity: 0.7;
  }
`;

const drift = keyframes`
  0% {
    transform: translateX(-50px);
  }
  50% {
    transform: translateX(50px);
  }
  100% {
    transform: translateX(-50px);
  }
`;

const AnimatedBackground: React.FC = () => {
  const symbols = [
    { icon: '💰', top: '10%', left: '10%', delay: '0s' },
    { icon: '🍕', top: '20%', left: '80%', delay: '2s' },
    { icon: '🚗', top: '30%', left: '15%', delay: '1s' },
    { icon: '📊', top: '40%', left: '75%', delay: '3s' },
    { icon: '💳', top: '50%', left: '20%', delay: '4s' },
    { icon: '🏠', top: '60%', left: '85%', delay: '1.5s' },
    { icon: '⚡', top: '70%', left: '10%', delay: '2.5s' },
    { icon: '📱', top: '80%', left: '70%', delay: '0.5s' },
    { icon: '🎯', top: '15%', left: '50%', delay: '3.5s' },
    { icon: '💡', top: '35%', left: '45%', delay: '1.8s' },
    { icon: '🛒', top: '55%', left: '60%', delay: '2.8s' },
    { icon: '✈️', top: '75%', left: '40%', delay: '0.8s' },
  ];

  return (
    <Box
      sx={{
        position: 'fixed',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        overflow: 'hidden',
        zIndex: -1,
        background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
        '&::before': {
          content: '""',
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'linear-gradient(45deg, rgba(255,255,255,0.1) 0%, rgba(255,255,255,0.05) 100%)',
        }
      }}
    >
      {symbols.map((symbol, index) => (
        <Box
          key={index}
          sx={{
            position: 'absolute',
            top: symbol.top,
            left: symbol.left,
            fontSize: '2rem',
            animation: `${float} 6s ease-in-out infinite ${symbol.delay}, ${drift} 12s ease-in-out infinite ${symbol.delay}`,
            userSelect: 'none',
            pointerEvents: 'none',
            filter: 'drop-shadow(0 4px 8px rgba(0,0,0,0.3))',
          }}
        >
          {symbol.icon}
        </Box>
      ))}
    </Box>
  );
};

export default AnimatedBackground;
