import React, { useState, ChangeEvent, FormEvent } from 'react';
import { TransactionFormProps, TransactionFormData, Transaction } from '../types';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  RadioGroup,
  FormControlLabel,
  Radio,
  SelectChangeEvent,
  FormLabel,
  Box,
  Typography,
  Slide,
  Fade,
} from '@mui/material';
import { TransitionProps } from '@mui/material/transitions';
import api from '../services/api';

const Transition = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>,
) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const categories = [
  { value: 'Food & Dining', emoji: '🍔', color: '#ef4444' },
  { value: 'Transportation', emoji: '🚗', color: '#3b82f6' },
  { value: 'Shopping', emoji: '🛍️', color: '#ec4899' },
  { value: 'Entertainment', emoji: '🎬', color: '#8b5cf6' },
  { value: 'Bills & Utilities', emoji: '📱', color: '#f59e0b' },
  { value: 'Healthcare', emoji: '🏥', color: '#10b981' },
  { value: 'Education', emoji: '📚', color: '#6366f1' },
  { value: 'Travel', emoji: '✈️', color: '#06b6d4' },
  { value: 'Income', emoji: '💰', color: '#10b981' },
  { value: 'Other', emoji: '📝', color: '#6b7280' },
];

const TransactionForm: React.FC<TransactionFormProps> = ({
  open,
  onClose,
  onSuccess,
  transaction,
}) => {
  const [formData, setFormData] = useState<TransactionFormData>({
    type: 'expense',
    amount: 0,
    category: '',
    description: '',
    date: new Date().toISOString().split('T')[0],
    ...transaction,
  });
  
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    try {
      if (transaction?._id) {
        await api.put(`/transactions/${transaction._id}`, formData);
      } else {
        await api.post('/transactions', formData);
      }
      onSuccess();
      onClose();
    } catch (error) {
      console.error('Error saving transaction:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (field: keyof TransactionFormData) => (
    e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement> | SelectChangeEvent
  ) => {
    setFormData((prev: TransactionFormData) => ({
      ...prev,
      [field]: e.target.value,
    }));
  };

  return (
    <Dialog 
      open={open} 
      onClose={onClose} 
      maxWidth="sm" 
      fullWidth
      TransitionComponent={Transition}
      sx={{
        '& .MuiDialog-paper': {
          borderRadius: 4,
          background: 'linear-gradient(135deg, rgba(255,255,255,0.95) 0%, rgba(255,255,255,0.9) 100%)',
          backdropFilter: 'blur(20px)',
          border: '1px solid rgba(255,255,255,0.3)',
        }
      }}
    >
      <form onSubmit={handleSubmit}>
        <DialogTitle sx={{ 
          background: 'linear-gradient(90deg, #6366f1, #8b5cf6)',
          color: 'white',
          fontWeight: 700,
          fontSize: '1.5rem'
        }}>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            {transaction ? '✏️ Edit Transaction' : '➕ Add Transaction'}
            {loading && (
              <Typography variant="body2" sx={{ ml: 'auto', opacity: 0.9 }}>
                💾 Saving...
              </Typography>
            )}
          </Box>
        </DialogTitle>
        
        <DialogContent sx={{ pt: 3 }}>
          <FormControl component="fieldset" sx={{ mb: 3, width: '100%' }}>
            <FormLabel component="legend" sx={{ fontWeight: 600, mb: 1 }}>
              💳 Transaction Type
            </FormLabel>
            <RadioGroup
              row
              value={formData.type}
              onChange={handleChange('type')}
              sx={{ gap: 2 }}
            >
              <FormControlLabel
                value="income"
                control={<Radio sx={{ 
                  '&.Mui-checked': { 
                    color: '#10b981'
                  }
                }} />}
                label={
                  <Box sx={{ 
                    display: 'flex', 
                    alignItems: 'center', 
                    gap: 1,
                    p: 1,
                    borderRadius: 2,
                    background: formData.type === 'income' ? 'rgba(16,185,129,0.1)' : 'transparent',
                    border: formData.type === 'income' ? '2px solid #10b981' : '2px solid transparent',
                    transition: 'all 300ms ease'
                  }}>
                    📈 <Typography sx={{ fontWeight: 600 }}>Income</Typography>
                  </Box>
                }
              />
              <FormControlLabel
                value="expense"
                control={<Radio sx={{ 
                  '&.Mui-checked': { 
                    color: '#ef4444'
                  }
                }} />}
                label={
                  <Box sx={{ 
                    display: 'flex', 
                    alignItems: 'center', 
                    gap: 1,
                    p: 1,
                    borderRadius: 2,
                    background: formData.type === 'expense' ? 'rgba(239,68,68,0.1)' : 'transparent',
                    border: formData.type === 'expense' ? '2px solid #ef4444' : '2px solid transparent',
                    transition: 'all 300ms ease'
                  }}>
                    📉 <Typography sx={{ fontWeight: 600 }}>Expense</Typography>
                  </Box>
                }
              />
            </RadioGroup>
          </FormControl>

          <TextField
            fullWidth
            label="💰 Amount"
            type="number"
            value={formData.amount}
            onChange={handleChange('amount')}
            required
            sx={{ 
              mb: 3,
              '& .MuiOutlinedInput-root': {
                borderRadius: 3,
                '&:hover': {
                  transform: 'translateY(-1px)',
                },
                '&.Mui-focused': {
                  transform: 'translateY(-2px)',
                  boxShadow: '0 8px 25px rgba(99, 102, 241, 0.15)',
                },
              }
            }}
          />

          <FormControl fullWidth sx={{ mb: 3 }}>
            <InputLabel sx={{ fontWeight: 600 }}>🎯 Category</InputLabel>
            <Select
              value={formData.category}
              onChange={(e) =>
                setFormData(prev => ({ ...prev, category: e.target.value }))
              }
              required
              sx={{
                borderRadius: 3,
                '&:hover': {
                  transform: 'translateY(-1px)',
                },
                '&.Mui-focused': {
                  transform: 'translateY(-2px)',
                  boxShadow: '0 8px 25px rgba(99, 102, 241, 0.15)',
                },
              }}
            >
              {categories.map((category) => (
                <MenuItem 
                  key={category.value} 
                  value={category.value}
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: 1,
                    '&:hover': {
                      background: `${category.color}15`,
                    }
                  }}
                >
                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <span style={{ fontSize: '1.2rem' }}>{category.emoji}</span>
                    <Typography>{category.value}</Typography>
                  </Box>
                </MenuItem>
              ))}
            </Select>
          </FormControl>

          <TextField
            fullWidth
            label="📝 Description"
            value={formData.description}
            onChange={handleChange('description')}
            multiline
            rows={2}
            sx={{ 
              mb: 3,
              '& .MuiOutlinedInput-root': {
                borderRadius: 3,
                '&:hover': {
                  transform: 'translateY(-1px)',
                },
                '&.Mui-focused': {
                  transform: 'translateY(-2px)',
                  boxShadow: '0 8px 25px rgba(99, 102, 241, 0.15)',
                },
              }
            }}
          />

          <TextField
            fullWidth
            label="📅 Date"
            type="date"
            value={formData.date}
            onChange={handleChange('date')}
            required
            InputLabelProps={{ shrink: true }}
            sx={{
              '& .MuiOutlinedInput-root': {
                borderRadius: 3,
                '&:hover': {
                  transform: 'translateY(-1px)',
                },
                '&.Mui-focused': {
                  transform: 'translateY(-2px)',
                  boxShadow: '0 8px 25px rgba(99, 102, 241, 0.15)',
                },
              }
            }}
          />
        </DialogContent>

        <DialogActions sx={{ p: 3, gap: 2 }}>
          <Button 
            onClick={onClose}
            variant="outlined"
            size="large"
            sx={{ 
              fontWeight: 600,
              borderWidth: 2,
              '&:hover': {
                borderWidth: 2,
              }
            }}
          >
            ❌ Cancel
          </Button>
          <Button 
            type="submit" 
            variant="contained" 
            disabled={loading}
            size="large"
            sx={{
              fontWeight: 700,
              px: 3,
              background: formData.type === 'income' 
                ? 'linear-gradient(45deg, #10b981, #34d399)' 
                : 'linear-gradient(45deg, #ef4444, #f87171)',
              '&:hover': {
                background: formData.type === 'income' 
                  ? 'linear-gradient(45deg, #059669, #10b981)' 
                  : 'linear-gradient(45deg, #dc2626, #ef4444)',
              },
              '&:disabled': {
                background: '#9ca3af',
              }
            }}
          >
            {loading ? '💾 Saving...' : (transaction ? '✏️ Update' : '💾 Save')}
          </Button>
        </DialogActions>
      </form>
    </Dialog>
  );
};

export default TransactionForm;
