import React, { useEffect, useState } from 'react';
import {
  Container,
  Typography,
  Box,
  Card,
  CardContent,
  Grid,
  Paper,
  Tabs,
  Tab,
  useTheme,
} from '@mui/material';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement,
} from 'chart.js';
import { Line, Bar, Pie } from 'react-chartjs-2';
import { useAuth } from '../context/AuthContext';
import api from '../services/api';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement
);

interface Transaction {
  _id: string;
  amount: number;
  description: string;
  category: string;
  type: 'income' | 'expense';
  date: string;
}

interface Summary {
  totalIncome: number;
  totalExpenses: number;
  savings: number;
  categoryBreakdown: { [key: string]: number };
}

const Dashboard: React.FC = () => {
  const { user } = useAuth();
  const theme = useTheme();
  const [transactions, setTransactions] = useState<Transaction[]>([]);
  const [tabValue, setTabValue] = useState(0);
  const [summary, setSummary] = useState<Summary>({
    totalIncome: 0,
    totalExpenses: 0,
    savings: 0,
    categoryBreakdown: {}
  });

  useEffect(() => {
    const fetchData = async () => {
      try {
        const transactionsResponse = await api.get('/transactions');
        const transactionData = transactionsResponse.data as Transaction[];
        setTransactions(transactionData);
        
        // Calculate summary
        const income = transactionData
          .filter((t: Transaction) => t.type === 'income')
          .reduce((sum: number, t: Transaction) => sum + t.amount, 0);
        
        const expenses = transactionData
          .filter((t: Transaction) => t.type === 'expense')
          .reduce((sum: number, t: Transaction) => sum + t.amount, 0);
        
        const categoryBreakdown: { [key: string]: number } = {};
        transactionData
          .filter((t: Transaction) => t.type === 'expense')
          .forEach((t: Transaction) => {
            categoryBreakdown[t.category] = (categoryBreakdown[t.category] || 0) + t.amount;
          });
        
        setSummary({
          totalIncome: income,
          totalExpenses: expenses,
          savings: income - expenses,
          categoryBreakdown
        });
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
  }, []);

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };

  // Prepare data for Income vs Expenses Line Chart
  const getIncomeExpenseChartData = () => {
    const last6Months = [];
    const incomeData = [];
    const expenseData = [];
    
    for (let i = 5; i >= 0; i--) {
      const date = new Date();
      date.setMonth(date.getMonth() - i);
      const monthYear = date.toLocaleDateString('en-US', { month: 'short', year: 'numeric' });
      last6Months.push(monthYear);
      
      const monthTransactions = transactions.filter(t => {
        const transactionDate = new Date(t.date);
        return transactionDate.getMonth() === date.getMonth() && 
               transactionDate.getFullYear() === date.getFullYear();
      });
      
      const monthIncome = monthTransactions
        .filter(t => t.type === 'income')
        .reduce((sum, t) => sum + t.amount, 0);
      const monthExpenses = monthTransactions
        .filter(t => t.type === 'expense')
        .reduce((sum, t) => sum + t.amount, 0);
      
      incomeData.push(monthIncome);
      expenseData.push(monthExpenses);
    }

    return {
      labels: last6Months,
      datasets: [
        {
          label: 'Income',
          data: incomeData,
          borderColor: 'rgb(75, 192, 192)',
          backgroundColor: 'rgba(75, 192, 192, 0.2)',
          tension: 0.4,
        },
        {
          label: 'Expenses',
          data: expenseData,
          borderColor: 'rgb(255, 99, 132)',
          backgroundColor: 'rgba(255, 99, 132, 0.2)',
          tension: 0.4,
        },
      ],
    };
  };

  // Prepare data for Category Pie Chart
  const getCategoryChartData = () => {
    const categories = Object.keys(summary.categoryBreakdown);
    const amounts = Object.values(summary.categoryBreakdown);
    
    const colors = [
      '#FF6384', '#36A2EB', '#FFCE56', '#4BC0C0', 
      '#9966FF', '#FF9F40', '#FF6384', '#C9CBCF'
    ];

    return {
      labels: categories,
      datasets: [
        {
          data: amounts,
          backgroundColor: colors.slice(0, categories.length),
          borderColor: colors.slice(0, categories.length).map(color => color.replace('0.8', '1')),
          borderWidth: 2,
        },
      ],
    };
  };

  const chartOptions = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top' as const,
      },
      title: {
        display: true,
        text: 'Financial Overview',
      },
    },
    scales: {
      y: {
        beginAtZero: true,
      },
    },
  };

  const pieChartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'bottom' as const,
        labels: {
          padding: 20,
          usePointStyle: true,
        }
      },
      title: {
        display: true,
        text: 'Expense Categories Breakdown',
        font: {
          size: 16,
          weight: 'bold' as const,
        }
      },
    },
  };

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      {/* Hero Section */}
      <Box
        sx={{
          background: 'linear-gradient(135deg, rgba(102, 126, 234, 0.1) 0%, rgba(118, 75, 162, 0.1) 100%)',
          backdropFilter: 'blur(20px)',
          borderRadius: 3,
          border: '1px solid rgba(102, 126, 234, 0.2)',
          p: 4,
          mb: 4,
          textAlign: 'center',
        }}
      >
        <Typography
          variant="h3"
          sx={{
            background: 'linear-gradient(45deg, #667eea 30%, #764ba2 90%)',
            backgroundClip: 'text',
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent',
            fontWeight: 'bold',
            mb: 2,
          }}
        >
          💰 Welcome Back, {user?.name}!
        </Typography>
        <Typography variant="h6" color="text.secondary">
          Here's your financial overview
        </Typography>
      </Box>

      {/* Financial Cards */}
      <Grid container spacing={3} sx={{ mb: 4 }}>
        <Grid item xs={12} sm={6} md={4}>
          <Card
            sx={{
              background: 'linear-gradient(135deg, #4CAF50 0%, #81C784 100%)',
              color: 'white',
              transition: 'all 0.3s ease-in-out',
              '&:hover': {
                transform: 'translateY(-5px)',
                boxShadow: '0 10px 25px rgba(76, 175, 80, 0.3)',
              },
            }}
          >
            <CardContent>
              <Typography variant="h6" sx={{ mb: 1 }}>
                📈 Total Income
              </Typography>
              <Typography variant="h4" sx={{ fontWeight: 'bold' }}>
                ₹{summary.totalIncome.toLocaleString()}
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={4}>
          <Card
            sx={{
              background: 'linear-gradient(135deg, #f44336 0%, #ef5350 100%)',
              color: 'white',
              transition: 'all 0.3s ease-in-out',
              '&:hover': {
                transform: 'translateY(-5px)',
                boxShadow: '0 10px 25px rgba(244, 67, 54, 0.3)',
              },
            }}
          >
            <CardContent>
              <Typography variant="h6" sx={{ mb: 1 }}>
                📉 Total Expenses
              </Typography>
              <Typography variant="h4" sx={{ fontWeight: 'bold' }}>
                ₹{summary.totalExpenses.toLocaleString()}
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={4}>
          <Card
            sx={{
              background: summary.savings >= 0 
                ? 'linear-gradient(135deg, #2196F3 0%, #64B5F6 100%)'
                : 'linear-gradient(135deg, #FF9800 0%, #FFB74D 100%)',
              color: 'white',
              transition: 'all 0.3s ease-in-out',
              '&:hover': {
                transform: 'translateY(-5px)',
                boxShadow: summary.savings >= 0 
                  ? '0 10px 25px rgba(33, 150, 243, 0.3)'
                  : '0 10px 25px rgba(255, 152, 0, 0.3)',
              },
            }}
          >
            <CardContent>
              <Typography variant="h6" sx={{ mb: 1 }}>
                {summary.savings >= 0 ? '💎 Savings' : '⚠️ Deficit'}
              </Typography>
              <Typography variant="h4" sx={{ fontWeight: 'bold' }}>
                ₹{Math.abs(summary.savings).toLocaleString()}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* Category Breakdown */}
      <Paper
        sx={{
          p: 3,
          background: 'rgba(255, 255, 255, 0.95)',
          backdropFilter: 'blur(20px)',
          borderRadius: 3,
          border: '1px solid rgba(0, 0, 0, 0.1)',
        }}
      >
        <Typography variant="h5" sx={{ mb: 3, fontWeight: 'bold' }}>
          📊 Expense Categories
        </Typography>
        
        {Object.keys(summary.categoryBreakdown).length > 0 ? (
          <Grid container spacing={2}>
            {Object.entries(summary.categoryBreakdown).map(([category, amount]) => (
              <Grid item xs={12} sm={6} md={4} key={category}>
                <Box
                  sx={{
                    p: 2,
                    borderRadius: 2,
                    background: 'linear-gradient(135deg, rgba(102, 126, 234, 0.1) 0%, rgba(118, 75, 162, 0.1) 100%)',
                    border: '1px solid rgba(102, 126, 234, 0.2)',
                    transition: 'all 0.3s ease-in-out',
                    '&:hover': {
                      transform: 'scale(1.02)',
                      background: 'linear-gradient(135deg, rgba(102, 126, 234, 0.15) 0%, rgba(118, 75, 162, 0.15) 100%)',
                    },
                  }}
                >
                  <Typography variant="subtitle1" sx={{ fontWeight: 'bold', textTransform: 'capitalize' }}>
                    {category}
                  </Typography>
                  <Typography variant="h6" color="primary">
                    ₹{amount.toLocaleString()}
                  </Typography>
                </Box>
              </Grid>
            ))}
          </Grid>
        ) : (
          <Typography variant="body1" color="text.secondary" sx={{ textAlign: 'center', py: 4 }}>
            No expense data available yet. Start adding transactions to see your spending breakdown!
          </Typography>
        )}
      </Paper>

      {/* Charts Section with Tabs */}
      <Paper
        sx={{
          mt: 4,
          p: 3,
          background: 'rgba(255, 255, 255, 0.95)',
          backdropFilter: 'blur(20px)',
          borderRadius: 3,
          border: '1px solid rgba(0, 0, 0, 0.1)',
        }}
      >
        <Typography variant="h5" sx={{ mb: 3, fontWeight: 'bold' }}>
          📈 Financial Analytics
        </Typography>
        
        <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}>
          <Tabs 
            value={tabValue} 
            onChange={handleTabChange} 
            aria-label="financial charts tabs"
            sx={{
              '& .MuiTab-root': {
                textTransform: 'none',
                fontWeight: 'bold',
                fontSize: '1rem',
              },
            }}
          >
            <Tab 
              label="📊 Income vs Expenses" 
              sx={{ 
                color: 'primary.main',
                '&.Mui-selected': {
                  background: 'linear-gradient(135deg, rgba(102, 126, 234, 0.1) 0%, rgba(118, 75, 162, 0.1) 100%)',
                  borderRadius: 2,
                }
              }} 
            />
            <Tab 
              label="🥧 Category Breakdown" 
              sx={{ 
                color: 'primary.main',
                '&.Mui-selected': {
                  background: 'linear-gradient(135deg, rgba(102, 126, 234, 0.1) 0%, rgba(118, 75, 162, 0.1) 100%)',
                  borderRadius: 2,
                }
              }} 
            />
          </Tabs>
        </Box>

        {/* Tab Panels */}
        {tabValue === 0 && (
          <Box sx={{ height: 400, p: 2 }}>
            {transactions.length > 0 ? (
              <Line data={getIncomeExpenseChartData()} options={chartOptions} />
            ) : (
              <Box 
                sx={{ 
                  display: 'flex', 
                  justifyContent: 'center', 
                  alignItems: 'center', 
                  height: '100%',
                  flexDirection: 'column',
                  gap: 2,
                }}
              >
                <Typography variant="h6" color="text.secondary">
                  📊 No Transaction Data
                </Typography>
                <Typography variant="body2" color="text.secondary" sx={{ textAlign: 'center' }}>
                  Add some transactions to see your income vs expenses trend over time!
                </Typography>
              </Box>
            )}
          </Box>
        )}

        {tabValue === 1 && (
          <Box sx={{ height: 400, p: 2, display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
            {Object.keys(summary.categoryBreakdown).length > 0 ? (
              <Box sx={{ width: '100%', maxWidth: '500px', height: '350px' }}>
                <Pie data={getCategoryChartData()} options={pieChartOptions} />
              </Box>
            ) : (
              <Box 
                sx={{ 
                  display: 'flex', 
                  justifyContent: 'center', 
                  alignItems: 'center', 
                  height: '100%',
                  flexDirection: 'column',
                  gap: 2,
                }}
              >
                <Typography variant="h6" color="text.secondary">
                  🥧 No Category Data
                </Typography>
                <Typography variant="body2" color="text.secondary" sx={{ textAlign: 'center' }}>
                  Add some expense transactions to see your spending breakdown by category!
                </Typography>
              </Box>
            )}
          </Box>
        )}
      </Paper>
    </Container>
  );
};

export default Dashboard;
