import React from 'react';
import { Box, AppBar, Toolbar, Typography, Button, Container, Fab, useTheme } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import { useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

const Layout: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const navigate = useNavigate();
  const location = useLocation();
  const { logout } = useAuth();

  const handleNavigation = (path: string) => {
    navigate(path);
  };

  const handleLogout = () => {
    logout();
    navigate('/login');
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
      <AppBar
        position="static"
        sx={{
          background: 'linear-gradient(90deg, rgba(255,112,67,1) 0%, rgba(255,178,102,0.9) 40%, rgba(0,105,92,1) 100%)',
          borderBottom: theme => `1px solid ${theme.palette.divider}`,
          backdropFilter: 'saturate(120%) blur(6px)'
        }}
      >
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1, fontWeight: 700 }}>
            Budget Tracker
          </Typography>
          {/* Animated nav items */}
          <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
            {[
              { label: 'Dashboard', path: '/' },
              { label: 'Transactions', path: '/transactions' },
              { label: 'Profile', path: '/profile' },
            ].map(item => {
              const active = location.pathname === item.path;
              return (
                <Button
                  key={item.path}
                  onClick={() => handleNavigation(item.path)}
                  disableElevation
                  sx={{
                    position: 'relative',
                    px: 2.2,
                    py: 0.7,
                    color: active ? 'common.white' : 'rgba(255,255,255,0.95)',
                    background: active ? 'rgba(255,255,255,0.12)' : 'transparent',
                    borderRadius: 2,
                    textTransform: 'none',
                    fontWeight: 600,
                    transition: 'transform 220ms cubic-bezier(.2,.8,.2,1), box-shadow 220ms, background 220ms',
                    transform: 'translateZ(0)',
                    boxShadow: active ? '0 6px 18px rgba(0,0,0,0.12)' : 'none',
                    '&:hover': {
                      transform: 'translateY(-4px) scale(1.02)',
                      background: 'linear-gradient(90deg, rgba(255,255,255,0.12), rgba(255,255,255,0.06))',
                      boxShadow: '0 10px 20px rgba(0,0,0,0.14)',
                    },
                    '&::after': {
                      content: '""',
                      position: 'absolute',
                      left: 12,
                      right: 12,
                      bottom: -6,
                      height: 4,
                      borderRadius: 2,
                      background: active ? 'linear-gradient(90deg,#ffd7b5,#ff9a8b)' : 'transparent',
                      transition: 'all 260ms ease',
                      opacity: active ? 1 : 0,
                      transform: active ? 'scaleX(1)' : 'scaleX(0.6)'
                    }
                  }}
                >
                  {item.label}
                </Button>
              );
            })}
          </Box>
          <Button color="inherit" onClick={handleLogout} sx={{ ml: 2 }}>
            Logout
          </Button>
        </Toolbar>
      </AppBar>

      <Container component="main" sx={{ flex: 1, py: 3 }}>
        {children}
      </Container>

      {/* Floating Add button for quick transactions */}
      <Fab
        color="secondary"
        aria-label="add"
        sx={{ position: 'fixed', right: 20, bottom: 24, transition: 'transform 200ms ease, box-shadow 200ms ease', '&:hover': { transform: 'translateY(-6px) scale(1.06)', boxShadow: theme => `0 12px 30px ${theme.palette.mode === 'light' ? 'rgba(0,0,0,0.18)' : 'rgba(0,0,0,0.4)'}` } }}
        onClick={() => handleNavigation('/transactions')}
      >
        <AddIcon />
      </Fab>
    </Box>
  );
};

export default Layout;
