import React, { useState, useEffect } from 'react';
import {
  Container,
  Paper,
  TextField,
  Button,
  Typography,
  Box,
  Link,
  Fade,
  Slide,
  Alert,
  AlertTitle,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import AnimatedBackground from './AnimatedBackground';

const Login: React.FC = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [errorDetails, setErrorDetails] = useState('');
  const [loading, setLoading] = useState(false);
  
  useEffect(() => {
    try {
      const saved = localStorage.getItem('savedEmail');
      if (saved) setEmail(saved);
    } catch (e) {
      // Silent fail for localStorage access
    }
  }, []);
  
  const navigate = useNavigate();
  const { login } = useAuth();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    setErrorDetails('');

    try {
      // Save email for next time
      localStorage.setItem('savedEmail', email);
      
      await login(email, password);
      navigate('/');
    } catch (error: any) {
      console.error('Login error:', error);
      
      const errorResponse = error?.response?.data;
      if (errorResponse) {
        setError(errorResponse.message || 'Login failed');
        setErrorDetails(errorResponse.details || '');
      } else {
        setError('Login failed');
        setErrorDetails('Please check your internet connection and try again.');
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <>
      <AnimatedBackground />
      <Box sx={{ 
        minHeight: '100vh',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        position: 'relative',
        zIndex: 1,
      }}>
        <Container maxWidth="xs" sx={{ position: 'relative', zIndex: 1 }}>
          <Slide direction="up" in={true} timeout={800}>
            <Paper
              elevation={24}
              sx={{
                p: 4,
                borderRadius: 4,
                background: 'linear-gradient(135deg, rgba(255,255,255,0.95) 0%, rgba(255,255,255,0.9) 100%)',
                backdropFilter: 'blur(20px)',
                border: '1px solid rgba(255,255,255,0.3)',
                boxShadow: '0 32px 64px rgba(0,0,0,0.15)',
              }}
            >
            <Box sx={{ textAlign: 'center', mb: 4 }}>
              <Typography 
                variant="h3" 
                sx={{ 
                  fontWeight: 800,
                  background: 'linear-gradient(45deg, #6366f1, #ec4899)',
                  backgroundClip: 'text',
                  WebkitBackgroundClip: 'text',
                  WebkitTextFillColor: 'transparent',
                  mb: 1
                }}
              >
                💰 Budget Tracker
              </Typography>
              <Typography 
                variant="h5" 
                sx={{ 
                  fontWeight: 600,
                  color: 'text.primary',
                  mb: 1
                }}
              >
                Welcome Back! 👋
              </Typography>
              <Typography variant="body1" color="text.secondary">
                Sign in to manage your finances with style
              </Typography>
            </Box>
            <Box component="form" onSubmit={handleSubmit} sx={{ mt: 3 }}>
              <TextField
                margin="normal"
                required
                fullWidth
                id="email"
                label="📧 Email Address"
                name="email"
                autoComplete="email"
                autoFocus
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                sx={{
                  mb: 2,
                  '& .MuiOutlinedInput-root': {
                    borderRadius: 3,
                  }
                }}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                name="password"
                label="🔒 Password"
                type="password"
                id="password"
                autoComplete="current-password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                sx={{
                  mb: 3,
                  '& .MuiOutlinedInput-root': {
                    borderRadius: 3,
                  }
                }}
              />
              
              {error && (
                <Alert 
                  severity="error" 
                  sx={{ 
                    mb: 2,
                    borderRadius: 2,
                  }}
                >
                  <AlertTitle>{error}</AlertTitle>
                  {errorDetails && (
                    <Typography variant="body2" sx={{ mt: 1 }}>
                      {errorDetails}
                    </Typography>
                  )}
                </Alert>
              )}
              <Button
                type="submit"
                fullWidth
                variant="contained"
                size="large"
                disabled={loading}
                sx={{ 
                  mt: 2, 
                  mb: 3,
                  py: 1.5,
                  fontSize: '1.1rem',
                  fontWeight: 'bold',
                  borderRadius: 3,
                  textTransform: 'none',
                  background: loading 
                    ? 'rgba(99, 102, 241, 0.5)' 
                    : 'linear-gradient(45deg, #6366f1, #8b5cf6)',
                  transition: 'all 0.3s ease-in-out',
                  '&:hover': {
                    transform: loading ? 'none' : 'translateY(-2px)',
                    boxShadow: loading ? 'none' : '0 12px 24px rgba(99, 102, 241, 0.4)',
                    background: loading 
                      ? 'rgba(99, 102, 241, 0.5)'
                      : 'linear-gradient(45deg, #4f46e5, #7c3aed)',
                  }
                }}
              >
                {loading ? 'Signing In...' : 'Sign In ✨'}
              </Button>
            </Box>
            <Box sx={{ textAlign: 'center' }}>
                <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
                  Don't have an account?
                </Typography>
                <Button
                  onClick={() => navigate('/register')}
                  variant="outlined"
                  sx={{ 
                    fontWeight: 600,
                    borderWidth: 2,
                    '&:hover': {
                      borderWidth: 2,
                    }
                  }}
                >
                  ✨ Create Account
                </Button>
              </Box>
            </Paper>
          </Slide>
        </Container>
      </Box>
    </>
  );
};

export default Login;
