import React, { useState, useEffect, useRef } from 'react';
import {
  Container,
  Paper,
  TextField,
  Button,
  Typography,
  Box,
  Grid,
  Avatar,
  IconButton,
  Tabs,
  Tab,
  Alert,
  CircularProgress,
  Card,
  CardContent,
} from '@mui/material';
import api from '../services/api';
import { useAuth } from '../context/AuthContext';

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`profile-tabpanel-${index}`}
      aria-labelledby={`profile-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          {children}
        </Box>
      )}
    </div>
  );
}

const Profile: React.FC = () => {
  const { user, logout } = useAuth();
  const [tabValue, setTabValue] = useState(0);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [error, setError] = useState('');
  const fileInputRef = useRef<HTMLInputElement>(null);
  
  // Profile Info State
  const [name, setName] = useState(user?.name || '');
  const [email, setEmail] = useState(user?.email || '');
  const [profilePicture, setProfilePicture] = useState<string>('');
  
  // Password Change State
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');

  useEffect(() => {
    setName(user?.name || '');
    setEmail(user?.email || '');
    if (user?.profilePicture) {
      setProfilePicture(user.profilePicture);
    }
  }, [user]);

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
    setMessage('');
    setError('');
  };

  const handleProfilePictureChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      if (file.size > 5 * 1024 * 1024) {
        setError('File size must be less than 5MB');
        return;
      }
      
      if (!file.type.startsWith('image/')) {
        setError('Please select an image file');
        return;
      }

      const reader = new FileReader();
      reader.onload = (e) => {
        const base64String = e.target?.result as string;
        setProfilePicture(base64String);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleProfileUpdate = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    setMessage('');
    
    try {
      const updateData: any = { name, email };
      if (profilePicture) {
        updateData.profilePicture = profilePicture;
      }
      
      await api.put('/auth/me', updateData);
      setMessage('Profile updated successfully! ✨');
      
      setTimeout(() => setMessage(''), 3000);
    } catch (error: any) {
      console.error('Profile update error:', error);
      setError(error.response?.data?.message || 'Failed to update profile');
    } finally {
      setLoading(false);
    }
  };

  const handlePasswordChange = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    setMessage('');
    
    if (newPassword !== confirmPassword) {
      setError('New passwords do not match');
      setLoading(false);
      return;
    }
    
    if (newPassword.length < 6) {
      setError('New password must be at least 6 characters long');
      setLoading(false);
      return;
    }
    
    try {
      await api.put('/auth/change-password', {
        currentPassword,
        newPassword
      });
      
      setMessage('Password changed successfully! Please login again.');
      setCurrentPassword('');
      setNewPassword('');
      setConfirmPassword('');
      
      setTimeout(() => {
        logout();
      }, 2000);
    } catch (error: any) {
      console.error('Password change error:', error);
      setError(error.response?.data?.message || 'Failed to change password');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container maxWidth="md" sx={{ mt: 4, mb: 4 }}>
      <Card 
        elevation={8}
        sx={{ 
          borderRadius: 4,
          background: 'linear-gradient(135deg, rgba(255,255,255,0.95) 0%, rgba(255,255,255,0.9) 100%)',
          backdropFilter: 'blur(20px)',
        }}
      >
        <CardContent sx={{ p: 0 }}>
          {/* Header */}
          <Box sx={{ 
            background: 'linear-gradient(135deg, #4f46e5 0%, #7c3aed 100%)',
            color: 'white',
            p: 4,
            textAlign: 'center',
            borderRadius: '16px 16px 0 0'
          }}>
            <Avatar
              src={profilePicture || undefined}
              sx={{ 
                width: 120, 
                height: 120, 
                margin: '0 auto 16px',
                fontSize: '3rem',
                border: '4px solid rgba(255,255,255,0.3)',
                background: profilePicture ? 'transparent' : 'linear-gradient(45deg, #ff6b6b, #4ecdc4)',
              }}
            >
              {!profilePicture && (user?.name?.charAt(0).toUpperCase() || 'U')}
            </Avatar>
            <Typography variant="h4" sx={{ fontWeight: 'bold', mb: 1 }}>
              {user?.name || 'User'}
            </Typography>
            <Typography variant="body1" sx={{ opacity: 0.9 }}>
              {user?.email || 'No email'}
            </Typography>
          </Box>

          {/* Tabs */}
          <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
            <Tabs 
              value={tabValue} 
              onChange={handleTabChange} 
              aria-label="profile tabs"
              centered
              sx={{
                '& .MuiTab-root': {
                  textTransform: 'none',
                  fontWeight: 'bold',
                  fontSize: '1rem',
                  minHeight: 72,
                  '&.Mui-selected': {
                    background: 'linear-gradient(135deg, rgba(79, 70, 229, 0.1) 0%, rgba(124, 58, 237, 0.1) 100%)',
                  }
                },
              }}
            >
              <Tab 
                label="👤 Profile Info" 
                sx={{ color: 'primary.main' }}
              />
              <Tab 
                label="🔒 Change Password" 
                sx={{ color: 'primary.main' }}
              />
            </Tabs>
          </Box>

          {/* Messages */}
          {message && (
            <Box sx={{ p: 2 }}>
              <Alert severity="success" sx={{ borderRadius: 2 }}>
                {message}
              </Alert>
            </Box>
          )}

          {error && (
            <Box sx={{ p: 2 }}>
              <Alert severity="error" sx={{ borderRadius: 2 }}>
                {error}
              </Alert>
            </Box>
          )}

          {/* Tab Panels */}
          <TabPanel value={tabValue} index={0}>
            <Box component="form" onSubmit={handleProfileUpdate}>
              <Grid container spacing={3}>
                {/* Profile Picture Section */}
                <Grid item xs={12} sx={{ textAlign: 'center', mb: 2 }}>
                  <Box sx={{ position: 'relative', display: 'inline-block' }}>
                    <Avatar
                      src={profilePicture || undefined}
                      sx={{ 
                        width: 150, 
                        height: 150, 
                        margin: '0 auto',
                        fontSize: '4rem',
                        border: '4px solid',
                        borderColor: 'primary.main',
                        background: profilePicture ? 'transparent' : 'linear-gradient(45deg, #ff6b6b, #4ecdc4)',
                      }}
                    >
                      {!profilePicture && (user?.name?.charAt(0).toUpperCase() || 'U')}
                    </Avatar>
                    <IconButton
                      sx={{
                        position: 'absolute',
                        bottom: 0,
                        right: 0,
                        background: 'primary.main',
                        color: 'white',
                        '&:hover': {
                          background: 'primary.dark',
                        },
                      }}
                      onClick={() => fileInputRef.current?.click()}
                    >
                      📷
                    </IconButton>
                    <input
                      ref={fileInputRef}
                      type="file"
                      hidden
                      accept="image/*"
                      onChange={handleProfilePictureChange}
                    />
                  </Box>
                  <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                    Click the camera icon to change your profile picture
                  </Typography>
                </Grid>

                {/* Name Field */}
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="👤 Full Name"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    required
                    sx={{
                      '& .MuiOutlinedInput-root': {
                        borderRadius: 3,
                      }
                    }}
                  />
                </Grid>

                {/* Email Field */}
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="📧 Email Address"
                    type="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                    sx={{
                      '& .MuiOutlinedInput-root': {
                        borderRadius: 3,
                      }
                    }}
                  />
                </Grid>

                {/* Action Buttons */}
                <Grid item xs={12}>
                  <Box sx={{ display: 'flex', gap: 2, justifyContent: 'flex-end' }}>
                    <Button
                      variant="outlined"
                      onClick={() => {
                        setName(user?.name || '');
                        setEmail(user?.email || '');
                        setProfilePicture('');
                      }}
                      sx={{ borderRadius: 3 }}
                    >
                      ❌ Reset
                    </Button>
                    <Button
                      type="submit"
                      variant="contained"
                      disabled={loading}
                      sx={{ 
                        borderRadius: 3,
                        background: 'linear-gradient(45deg, #4f46e5, #7c3aed)',
                        '&:hover': {
                          background: 'linear-gradient(45deg, #3730a3, #6b21a8)',
                        }
                      }}
                    >
                      {loading ? '🔄 Updating...' : '✅ Update Profile'}
                    </Button>
                  </Box>
                </Grid>
              </Grid>
            </Box>
          </TabPanel>

          <TabPanel value={tabValue} index={1}>
            <Box component="form" onSubmit={handlePasswordChange}>
              <Grid container spacing={3}>
                <Grid item xs={12}>
                  <Typography variant="h6" sx={{ mb: 2, color: 'text.primary' }}>
                    🔒 Change Your Password
                  </Typography>
                  <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
                    Ensure your password is strong and unique to keep your account secure.
                  </Typography>
                </Grid>

                {/* Current Password */}
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="🔑 Current Password"
                    type="password"
                    value={currentPassword}
                    onChange={(e) => setCurrentPassword(e.target.value)}
                    required
                    sx={{
                      '& .MuiOutlinedInput-root': {
                        borderRadius: 3,
                      }
                    }}
                  />
                </Grid>

                {/* New Password */}
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="🆕 New Password"
                    type="password"
                    value={newPassword}
                    onChange={(e) => setNewPassword(e.target.value)}
                    required
                    helperText="Password must be at least 6 characters long"
                    sx={{
                      '& .MuiOutlinedInput-root': {
                        borderRadius: 3,
                      }
                    }}
                  />
                </Grid>

                {/* Confirm New Password */}
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="✅ Confirm New Password"
                    type="password"
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                    required
                    error={confirmPassword !== '' && newPassword !== confirmPassword}
                    helperText={
                      confirmPassword !== '' && newPassword !== confirmPassword 
                        ? "Passwords don't match" 
                        : "Re-enter your new password"
                    }
                    sx={{
                      '& .MuiOutlinedInput-root': {
                        borderRadius: 3,
                      }
                    }}
                  />
                </Grid>

                {/* Action Buttons */}
                <Grid item xs={12}>
                  <Box sx={{ display: 'flex', gap: 2, justifyContent: 'flex-end' }}>
                    <Button
                      variant="outlined"
                      onClick={() => {
                        setCurrentPassword('');
                        setNewPassword('');
                        setConfirmPassword('');
                      }}
                      sx={{ borderRadius: 3 }}
                    >
                      🗑️ Clear
                    </Button>
                    <Button
                      type="submit"
                      variant="contained"
                      disabled={loading || !currentPassword || !newPassword || !confirmPassword || newPassword !== confirmPassword}
                      sx={{ 
                        borderRadius: 3,
                        background: 'linear-gradient(45deg, #ef4444, #f97316)',
                        '&:hover': {
                          background: 'linear-gradient(45deg, #dc2626, #ea580c)',
                        }
                      }}
                    >
                      {loading ? '🔄 Changing...' : '🔒 Change Password'}
                    </Button>
                  </Box>
                </Grid>
              </Grid>
            </Box>
          </TabPanel>
        </CardContent>
      </Card>
    </Container>
  );
};

export default Profile;
