import React from 'react';
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { AuthProvider, useAuth } from './context/AuthContext';
import { NotificationProvider } from './context/NotificationContext';
import Layout from './components/Layout';
import Dashboard from './components/Dashboard';
import TransactionList from './components/TransactionList';
import Profile from './components/Profile';
import Login from './components/Login';
import Register from './components/Register';

const theme = createTheme({
  palette: {
    mode: 'light',
    primary: {
      main: '#4f46e5', // indigo - better contrast
      light: '#6366f1',
      dark: '#3730a3',
      contrastText: '#ffffff'
    },
    secondary: {
      main: '#ec4899', // pink
      light: '#f472b6',
      dark: '#db2777',
      contrastText: '#ffffff'
    },
    success: {
      main: '#059669', // emerald - better contrast
      light: '#10b981',
      dark: '#047857',
      contrastText: '#ffffff'
    },
    warning: {
      main: '#d97706', // amber - better contrast
      light: '#f59e0b',
      dark: '#b45309',
      contrastText: '#ffffff'
    },
    error: {
      main: '#dc2626', // red - better contrast
      light: '#ef4444',
      dark: '#b91c1c',
      contrastText: '#ffffff'
    },
    info: {
      main: '#2563eb', // blue - better contrast
      light: '#3b82f6',
      dark: '#1d4ed8',
      contrastText: '#ffffff'
    },
    background: {
      default: '#f8fafc', // light gray for better readability
      paper: '#ffffff',
    },
  },
  typography: {
    fontFamily: "Inter, 'Segoe UI', Roboto, Arial, sans-serif",
    h1: { 
      fontWeight: 700,
      color: '#1e293b', // dark text for readability
    },
    h2: { 
      fontWeight: 600,
      color: '#1e293b',
    },
    h3: { 
      fontWeight: 600,
      color: '#1e293b',
    },
    h4: { 
      fontWeight: 600,
      color: '#1e293b',
    },
    h5: { 
      fontWeight: 500,
      color: '#1e293b',
    },
    h6: { 
      fontWeight: 500,
      color: '#1e293b',
    },
    body1: {
      color: '#334155', // better contrast for body text
    },
    body2: {
      color: '#64748b', // muted text
    },
  },
  shape: {
    borderRadius: 12,
  },
  components: {
    MuiCssBaseline: {
      styleOverrides: {
        body: {
          background: '#f8fafc',
          backgroundAttachment: 'fixed',
          minHeight: '100vh',
        },
      },
    },
    MuiButton: {
      defaultProps: { disableElevation: false },
      styleOverrides: {
        root: {
          borderRadius: 12,
          padding: '12px 24px',
          fontWeight: 600,
          textTransform: 'none',
          position: 'relative',
          overflow: 'hidden',
          transition: 'all 300ms cubic-bezier(0.4, 0, 0.2, 1)',
          '&:before': {
            content: '""',
            position: 'absolute',
            top: 0,
            left: '-100%',
            width: '100%',
            height: '100%',
            background: 'linear-gradient(90deg, transparent, rgba(255,255,255,0.3), transparent)',
            transition: 'left 600ms',
          },
          '&:hover': {
            transform: 'translateY(-2px) scale(1.02)',
            boxShadow: '0 20px 40px rgba(0,0,0,0.12)',
            '&:before': {
              left: '100%',
            },
          },
          '&:active': {
            transform: 'translateY(0) scale(0.98)',
          },
        },
        contained: {
          background: 'linear-gradient(45deg, #6366f1, #8b5cf6)',
          '&:hover': {
            background: 'linear-gradient(45deg, #4f46e5, #7c3aed)',
          },
        },
        outlined: {
          border: '2px solid',
          borderImageSlice: 1,
          borderImageSource: 'linear-gradient(45deg, #6366f1, #8b5cf6)',
          '&:hover': {
            borderImageSource: 'linear-gradient(45deg, #4f46e5, #7c3aed)',
            backgroundColor: 'rgba(99, 102, 241, 0.05)',
          },
        },
      }
    },
    MuiPaper: {
      styleOverrides: {
        root: {
          borderRadius: 20,
          backdropFilter: 'blur(20px)',
          background: 'rgba(255, 255, 255, 0.9)',
          border: '1px solid rgba(255, 255, 255, 0.2)',
          transition: 'all 300ms cubic-bezier(0.4, 0, 0.2, 1)',
          '&:hover': {
            transform: 'translateY(-4px)',
            boxShadow: '0 20px 50px rgba(0,0,0,0.1)',
          },
        }
      }
    },
    MuiCard: {
      styleOverrides: {
        root: {
          borderRadius: 20,
          backdropFilter: 'blur(20px)',
          background: 'linear-gradient(135deg, rgba(255,255,255,0.9) 0%, rgba(255,255,255,0.8) 100%)',
          border: '1px solid rgba(255, 255, 255, 0.3)',
          transition: 'all 400ms cubic-bezier(0.4, 0, 0.2, 1)',
          '&:hover': {
            transform: 'translateY(-8px) scale(1.02)',
            boxShadow: '0 25px 60px rgba(0,0,0,0.15)',
            background: 'linear-gradient(135deg, rgba(255,255,255,0.95) 0%, rgba(255,255,255,0.9) 100%)',
          },
        }
      }
    },
    MuiTextField: {
      styleOverrides: {
        root: {
          '& .MuiOutlinedInput-root': {
            borderRadius: 12,
            transition: 'all 300ms ease',
            '&:hover': {
              transform: 'translateY(-1px)',
              '& .MuiOutlinedInput-notchedOutline': {
                borderColor: '#6366f1',
                borderWidth: 2,
              },
            },
            '&.Mui-focused': {
              transform: 'translateY(-2px)',
              boxShadow: '0 8px 25px rgba(99, 102, 241, 0.15)',
            },
          },
        },
      },
    },
    MuiFab: {
      styleOverrides: {
        root: {
          background: 'linear-gradient(45deg, #ec4899, #f59e0b)',
          transition: 'all 300ms cubic-bezier(0.4, 0, 0.2, 1)',
          '&:hover': {
            background: 'linear-gradient(45deg, #db2777, #d97706)',
            transform: 'translateY(-4px) scale(1.1)',
            boxShadow: '0 15px 35px rgba(236, 72, 153, 0.4)',
          },
        },
      },
    },
    MuiAppBar: {
      styleOverrides: {
        root: {
          background: 'linear-gradient(135deg, rgba(255,255,255,0.95) 0%, rgba(255,255,255,0.85) 100%)',
          backdropFilter: 'blur(20px)',
          borderBottom: '1px solid rgba(255, 255, 255, 0.2)',
          boxShadow: '0 8px 32px rgba(0,0,0,0.1)',
        },
      },
    },
  },
});

const ProtectedRoute: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const { user, loading } = useAuth();

  if (loading) {
    return <div>Loading...</div>;
  }

  return user ? <>{children}</> : <Navigate to="/login" />;
};

const App: React.FC = () => {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <NotificationProvider>
        <AuthProvider>
          <Router>
            <Routes>
              <Route path="/login" element={<Login />} />
              <Route path="/register" element={<Register />} />
              <Route
                path="/*"
                element={
                  <ProtectedRoute>
                    <Layout>
                      <Routes>
                        <Route path="/" element={<Dashboard />} />
                        <Route path="/transactions" element={<TransactionList />} />
                        <Route path="/profile" element={<Profile />} />
                        <Route path="/dashboard" element={<Navigate to="/" />} />
                      </Routes>
                    </Layout>
                  </ProtectedRoute>
                }
              />
            </Routes>
          </Router>
        </AuthProvider>
      </NotificationProvider>
    </ThemeProvider>
  );
};

export default App;
