import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Paper,
  TextField,
  Button,
  Typography,
  Box,
  Link,
  Slide,
  Alert,
  AlertTitle,
} from '@mui/material';
import { useAuth } from '../context/AuthContext';
import AnimatedBackground from './AnimatedBackground';

const Register: React.FC = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [errorDetails, setErrorDetails] = useState('');
  const [suggestionAction, setSuggestionAction] = useState('');
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  const { register } = useAuth();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    setErrorDetails('');
    setSuggestionAction('');

    try {
      await register(name, email, password);
      navigate('/');
    } catch (error: any) {
      console.error('Registration error:', error);
      
      const errorResponse = error?.response?.data;
      if (errorResponse) {
        setError(errorResponse.message || 'Registration failed');
        setErrorDetails(errorResponse.details || '');
        setSuggestionAction(errorResponse.suggestionAction || '');
      } else {
        setError('Registration failed');
        setErrorDetails('Please check your internet connection and try again.');
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <>
      <AnimatedBackground />
      <Box
        sx={{
          minHeight: '100vh',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          position: 'relative',
          zIndex: 1,
        }}
      >
        <Container maxWidth="sm">
          <Slide direction="up" in={true} timeout={800}>
            <Paper
              elevation={24}
              sx={{
                padding: 4,
                background: 'linear-gradient(135deg, rgba(255,255,255,0.95) 0%, rgba(255,255,255,0.9) 100%)',
                backdropFilter: 'blur(20px)',
                borderRadius: 4,
                border: '1px solid rgba(255, 255, 255, 0.3)',
                boxShadow: '0 32px 64px rgba(0,0,0,0.15)',
              }}
            >
            <Box sx={{ textAlign: 'center', mb: 3 }}>
              <Typography
                variant="h3"
                sx={{
                  background: 'linear-gradient(45deg, #fff 30%, #f8f9ff 90%)',
                  backgroundClip: 'text',
                  WebkitBackgroundClip: 'text',
                  WebkitTextFillColor: 'transparent',
                  fontWeight: 'bold',
                  mb: 1,
                }}
              >
                🚀 Sign Up
              </Typography>
              <Typography variant="body1" sx={{ color: '#64748b' }}>
                Create your budget tracking account
              </Typography>
            </Box>

            <Box component="form" onSubmit={handleSubmit} sx={{ mt: 1 }}>
              <TextField
                margin="normal"
                required
                fullWidth
                id="name"
                label="👤 Full Name"
                name="name"
                autoComplete="name"
                autoFocus
                value={name}
                onChange={(e) => setName(e.target.value)}
                sx={{
                  '& .MuiOutlinedInput-root': {
                    backgroundColor: 'rgba(255, 255, 255, 0.1)',
                    backdropFilter: 'blur(10px)',
                    borderRadius: 2,
                    '& fieldset': {
                      borderColor: 'rgba(255, 255, 255, 0.3)',
                    },
                    '&:hover fieldset': {
                      borderColor: 'rgba(255, 255, 255, 0.5)',
                    },
                    '&.Mui-focused fieldset': {
                      borderColor: '#fff',
                    },
                  },
                  '& .MuiInputLabel-root': {
                    color: 'rgba(255, 255, 255, 0.8)',
                  },
                  '& .MuiOutlinedInput-input': {
                    color: '#fff',
                  },
                }}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                id="email"
                label="📧 Email Address"
                name="email"
                autoComplete="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                sx={{
                  '& .MuiOutlinedInput-root': {
                    backgroundColor: 'rgba(255, 255, 255, 0.1)',
                    backdropFilter: 'blur(10px)',
                    borderRadius: 2,
                    '& fieldset': {
                      borderColor: 'rgba(255, 255, 255, 0.3)',
                    },
                    '&:hover fieldset': {
                      borderColor: 'rgba(255, 255, 255, 0.5)',
                    },
                    '&.Mui-focused fieldset': {
                      borderColor: '#fff',
                    },
                  },
                  '& .MuiInputLabel-root': {
                    color: 'rgba(255, 255, 255, 0.8)',
                  },
                  '& .MuiOutlinedInput-input': {
                    color: '#fff',
                  },
                }}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                name="password"
                label="🔒 Password"
                type="password"
                id="password"
                autoComplete="new-password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                sx={{
                  '& .MuiOutlinedInput-root': {
                    backgroundColor: 'rgba(255, 255, 255, 0.1)',
                    backdropFilter: 'blur(10px)',
                    borderRadius: 2,
                    '& fieldset': {
                      borderColor: 'rgba(255, 255, 255, 0.3)',
                    },
                    '&:hover fieldset': {
                      borderColor: 'rgba(255, 255, 255, 0.5)',
                    },
                    '&.Mui-focused fieldset': {
                      borderColor: '#fff',
                    },
                  },
                  '& .MuiInputLabel-root': {
                    color: 'rgba(255, 255, 255, 0.8)',
                  },
                  '& .MuiOutlinedInput-input': {
                    color: '#fff',
                  },
                }}
              />
              
              {error && (
                <Alert 
                  severity="error" 
                  sx={{ 
                    mt: 2, 
                    backgroundColor: 'rgba(211, 47, 47, 0.1)',
                    backdropFilter: 'blur(10px)',
                    border: '1px solid rgba(211, 47, 47, 0.3)',
                    color: '#ffcdd2',
                    '& .MuiAlert-icon': {
                      color: '#ffcdd2',
                    }
                  }}
                >
                  <AlertTitle sx={{ color: '#ffcdd2' }}>{error}</AlertTitle>
                  {errorDetails && (
                    <Typography variant="body2" sx={{ mt: 1 }}>
                      {errorDetails}
                    </Typography>
                  )}
                  {suggestionAction === 'login' && (
                    <Box sx={{ mt: 2 }}>
                      <Link 
                        href="/login"
                        sx={{ 
                          color: '#ffcdd2',
                          textDecoration: 'underline',
                          '&:hover': {
                            color: '#fff',
                          },
                        }}
                      >
                        Go to Login Page →
                      </Link>
                    </Box>
                  )}
                </Alert>
              )}

              <Button
                type="submit"
                fullWidth
                variant="contained"
                disabled={loading}
                sx={{
                  mt: 3,
                  mb: 2,
                  py: 1.5,
                  background: loading 
                    ? 'rgba(255, 255, 255, 0.3)' 
                    : 'linear-gradient(45deg, #FF6B6B 30%, #4ECDC4 90%)',
                  borderRadius: 2,
                  fontWeight: 'bold',
                  fontSize: '1.1rem',
                  textTransform: 'none',
                  transition: 'all 0.3s ease-in-out',
                  '&:hover': {
                    transform: loading ? 'none' : 'translateY(-2px)',
                    boxShadow: loading ? 'none' : '0 10px 25px rgba(0,0,0,0.2)',
                    background: loading 
                      ? 'rgba(255, 255, 255, 0.3)'
                      : 'linear-gradient(45deg, #FF5252 30%, #26C6DA 90%)',
                  },
                  '&:disabled': {
                    color: 'rgba(255, 255, 255, 0.7)',
                  }
                }}
              >
                {loading ? 'Creating Account...' : 'Create Account ✨'}
              </Button>
            </Box>
            <Box sx={{ textAlign: 'center' }}>
                <Link 
                  href="/login"
                  sx={{ 
                    color: 'rgba(255,255,255,0.9)',
                    textDecoration: 'none',
                    '&:hover': {
                      color: '#fff',
                      textDecoration: 'underline',
                    },
                  }}
                >
                  Already have an account? Sign In
                </Link>
              </Box>
            </Paper>
          </Slide>
        </Container>
      </Box>
    </>
  );
};

export default Register;
